<script type="application/ld+json">
{
  '@type': 'Product',
  'name': {{ product.name }},
  'image': {{ product.productImage}},
  'description': {{ product.description }},
  'sku': {{ product.productCode }},
  'aggregateRating': {
  		'@type': 'AggregateRating',
  		'ratingValue': '4',
  		'reviewCount': '3'
  },
  'offers': {
  		'@type': 'Offer',
  		'priceCurrency': 'DKK',
  		'price': {{ product.price }},
  		'availability': 'http://schema.org/InStock'
  }
}
</script>

<script>
import axios from 'axios'
import jquery from '@jquery/dist/jquery.min.js'
export default {
  methods: {
    distract: function (counter) {
      alert(counter - 1)
    },
    tabs: function (num) {
      window.$ = jquery
      $(document).ready(function () {
        $('.tab').removeClass('tab_show')
        $('.tab' + num).addClass('tab_show')
        $('.tabs li').removeClass('active');
        $('.tab-l-' + num).addClass('active');
      })

    }
  },
  mounted () {
    var element = document.getElementById('header')
    element.classList.add('bg-white')
    var routeName = this.$route.params.id
    axios.get('static/products-data.json').then(response => (this.product = response.data.find(r => r.id === routeName)))
  },
  data () {
    return {
      product: null,
      isActive: false,
      heroImage: 'static/images/banner.jpg',
      counter: 1,
      quantity: 6,
      name: 'product',
      tab: 1
    }
  }
}

</script>

