<template>
  <div>
    <section class="banner" v-bind:style="{ backgroundImage: 'url(' + heroImage + ')' }">
      <div class="overlay py-5">
        <div class="container d-flex jf-center ai-center white-color fd-column uppercase">
          <h1 class="mb-2"><strong>Welcome</strong> to ave</h1>
          <p class="h3">Sign in or registrer</p>
        </div>
      </div>
    </section>
    <section class="signup mt-4">
      <div class="container-s">
        <div class="columns mt-3">

          <div class="col-6 column mb-4 border-r pt-0">
            <h2>Sign input</h2>
            <form class="d-flex" @submit.prevent="signIn">
              <input v-model="email" v-validate="'required|email'" :class="{'input': true, 'is-danger': errors.has('email') }" placeholder="Your Email.." type="email" name="email">
              <p>{{ errors.first('email') }}</p>
              <input v-model="password" v-validate="'required'" type="password" name="password" placeholder="Your Password..">
              <p>{{ errors.first('password') }}</p>
              <div class="d-flex ai-center">
                <button class="btn bg-cyan mt-1 button is-primary" type="submit">Sign in</button>
                <a class="ml-auto" href="#">Forgot your Password </a>
              </div>
            </form>
          </div>

          <div class="col-6 column mb-4 pt-0">
            <h2>Register</h2>
            <form class="d-flex" @submit.prevent="signIn">
              <input v-model="nemail" v-validate="'required|nemail'" placeholder="Your Email.." type="email" name="nemail">
              <span v-show="errors.has('nemail')" class="help is-danger">{{ errors.first('nemail') }}</span>
              <input v-model="npassword" v-validate="'required'" type="password" name="npassword" placeholder="Your Password..">
              <span v-show="errors.has('npassword')" class="help is-danger">{{ errors.first('npassword') }}</span>
              <input v-model="npassrepeat" v-validate="'required'" type="password" name="npassrepeat" placeholder="Confirm Password..">
              <span v-show="errors.has('npassrepeat')" class="help is-danger">{{ errors.first('npassrepeat') }}</span>
              <label class="d-flex ai-center check-el">
                <input v-model="newsletter" type="checkbox" name="newsletter">
                <div class="checkmark"></div>
                <p class="">Sign up for exclusive updates, discounts, new arrivals, contests, and more!</p>
              </label>
              <div class="d-flex jf-between">
                <button class="btn fb btn-border mr-5 button is-primary" type="submit">Create account</button>
                <p>By clicking Create Account, you agree to our <a href="#">Privacy Policy </a></p>
              </div>
            </form>

          </div>

        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  name: 'SingupPage',
  data () {
    return {
      heroImage: 'static/images/banner.jpg',
      email: '',
      nemail: '',
      npassrepeat: '',
      password: '',
      npassword: '',
      newsletter: false
    }
  },
  methods: {
    signIn () {
      this.$validator.validateAll().then((result) => {
        if (result) {
          // eslint-disable-next-line
          alert('Form Submitted!');
          return;
        }

        alert('Correct them errors!');
      });
    }
  },
  mounted () {
    var element = document.getElementById('header')
    element.classList.add('bg-white')
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



